.qcm-container {
  max-width: 600px;
  margin: auto;
  text-align: center;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.question-card {
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  font-size: 1.5rem;
  color: #333;
}

.timer {
  font-size: 1.2rem;
  color: #e63946;
  font-weight: bold;
}

.options-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 15px;
}

.option-button {
  padding: 12px;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #007bff;
  border: none;
  transition: background 0.3s ease-in-out;
  cursor: pointer;
}

.option-button:hover {
  background-color: #0056b3;
}

.results {
  padding: 20px;
  font-size: 1.3rem;
  font-weight: bold;
}

.success {
  color: #2ecc71;
}

.fail {
  color: #e74c3c;
}
